.weatherContainer {
	text-align: center; /*让div内部文字居中*/
	background-color: #f5f5f5;
	border-radius: 20px;
	width: calc(100vw * 0.9);
	height: calc(100vw * 0.9 * 0.46);
	max-width: 1500px;
	max-height: 690px;
	margin: auto;
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;

	.contentContainer {
		margin: calc(100vw * 0.9 * (125 / 1500));;
		display: flex;
		align-items: center;
		@media (min-width: 1500px) {
			margin: 125px;
		};
		.divider {
			border-right: 2px solid #d1d1d1;
			height: calc(100vw * 0.9 * 0.25);
    		max-height: 400px;
		}
	}
}
